@import './views/mobileView/styles/styles.css';

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'DM Sans';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@font-face {
  font-family: "DMSans-Regular"; 
  font-style: normal; 
  font-weight: normal;
  src: url("../src/assets/fonts/DMSans-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "DMSans-Medium"; 
  font-style: medium; 
  font-weight: normal;
  src: url("../src/assets/fonts/DMSans-Medium.ttf") format("truetype");
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


